.form {
  width: 68rem;
  height: fit-content;
  background-color: #ffffff;
  margin-bottom: 7.75rem;
  flex-direction: column;
  justify-content: center;
  padding: 4.5rem 1.5rem;
}

.form_header {
  justify-content: center;
  margin-bottom: 3rem;
}

.form_header>p {
  margin: 0;
  font-weight: 600;
  font-size: 2rem;
  line-height: 2.4rem;
  color: #018fde;
}

.form_header>p:last-child {
  padding-left: 0.7rem;
  color: #121212;
}

.form_inputs {
  max-width: 40rem;
  width: 100%;
}

.input_group,
.input-select_group {
  position: relative;
  width: 19.5rem;
  height: 3rem;
  margin-bottom: 1rem;
}

.textarea_group {
  position: relative;
  width: 40rem;
  height: 6rem;
  margin-bottom: 1.5rem;
}

.input_body,
.select_body,
.textarea_body {
  outline: none;
  border: none;
  width: 100%;
  height: 100%;
  background-color: #eef3fc;
  color: #8792a7;
  font-size: 0.875rem;
  padding-left: 1rem;
}

.select_body {
  position: absolute;
  top: 0;
  left: 0rem;
  width: 23%;
}

.input-select_group>.input_body {
  position: absolute;
  top: 0;
  right: 0;
  width: 77%;
}

.textarea_body{
  resize: none;
  padding: 1rem 1rem;
}

.input_body:focus,
.select_body:focus,
.textarea_body:focus {
  border: 1px solid #8792a7;
  border-radius: 1px;
  background-color: #ffffff;
}

.input_label,
.textarea_label {
  position: absolute;
  width: fit-content;
  background-color: #ffffff;
  top: -0.25rem;
  left: 1rem;
  font-size: 0.5rem;
  line-height: 0.5rem;
  opacity: 1;
  transform: translateY(0);
  transition: all 0.2s ease-out;
  color: #8792A7;
}

.input-select_group>.input_label {
  left: 6rem;
}

.input_body:placeholder-shown + .input_label, 
.textarea_body:placeholder-shown + .textarea_label {
  opacity: 0;
  transform: translateY(1rem);
  background-color: transparent;
}

.form>.button_wrapper {
  width: 13rem;
  z-index: 1;
}

.form>.button_wrapper>.button,
.form>.button_wrapper>div {
  width: 100%;
}

/* ---------------------------------media--------------------------------- */

@media only screen and (max-width: 768px) {
  .form {
    width: 39rem;
    padding: 2.5rem 1.5rem;
  }
  
  .form_header {
    min-width: fit-content;
    margin-bottom: 2rem;
  }
  
  .form_header>p {
    font-size: 1.75rem;
  }

  .form_inputs {
    max-width: 31rem;
  }
  
  .input_group,
  .input-select_group {
    width: 15rem;
  }
  
  .textarea_group {
    width: 31rem;
  }

  .select_body {
    width: 30%;
  }
  
  .input-select_group>.input_body {
    width: 70%;
  }
}

@media only screen and (max-width: 576px) {
  .form {
    width: 100%;
    padding: 1.5rem 1rem;
  }

  .form_header {
    margin-bottom: 1.5rem;
  }

  .form_header>p {
    font-size: 1.5rem;
  }
  
  .form_inputs {
    flex-direction: column;
  }

  .input_group,
  .input-select_group {
    height: 2.5rem;
  }

  .textarea_group {
    width: 15rem;
    height: 6.25rem;
  }

  .form>.button_wrapper {
    width: 15rem;
  }
}

