.navbar_wrapper {
    width: 100%;
    font-family: 'Montserrat', sans-serif;
}

.navbar {
    width: 100%;
    height: 5.5rem;
}

.navbar_nav {
    width: 50%;
    max-width: 26rem;
}

.navbar_logo {
    width: 6.9rem;
    z-index: 2;
}
.navbar_buttons {
    z-index: 2;
}

.navbar_link, a.nav-link {
    font-size: 0.875rem;
    line-height: 1.5rem;
    padding: 0;
    color: #8792A7;
    text-decoration: none;
    transition: all 0.3s linear;
}

nav .active {
    color: #121212;
}

a.nav-link:hover,
.navbar_link:hover {
    color: #121212;
}

/* ---------------------------dropdown--------------------------- */

a.dropdown-item,
.dropdown-item>a {
    padding: 0.3rem 0 0.3rem 0.5rem;
    font-size: 0.75rem;
    color: #8792A7;
    text-decoration: none;
}

a.dropdown-item:hover,
.dropdown-item>a:hover {
    color: #121212;
    background-color:transparent;
}

.dropdown-menu.show {
    min-width: 7.4rem;
    border: none;
    border-radius: 0;
    padding: 0;
}

#basic-nav-dropdown:focus {
    color: #121212
}

.dropdown-toggle::after {
    display: none;
}

.dropdown_arrow{
    margin-left: 0.4rem;
    width: 0.6rem;
}

/* ---------------------------accordion--------------------------- */

.accordion {
    display: none;
    border-bottom: 1px solid #dbdbdb;
}

.accordion-item {
    background-color: transparent;
}

button.accordion-button.collapsed {
    color: #121212;
    background-color: transparent;
    box-shadow: none;
}

.accordion-flush .accordion-button {
    color: #018fde;
    background-color: transparent;
    border: none;
    box-shadow: none;
}

.accordion-flush .accordion-item .accordion-button:hover {
    color: #018fde;
}

button.accordion-button {
    min-width: calc(100vw - 9rem);
    height: 5rem;
    padding: 0;
    font-size: 1.5rem;
    font-weight: 600;
    color: #121212;
    background-color: transparent;
}

button.accordion-button::after {
    color: #121212;
    margin-left: 1.23rem;
}

div.accordion-body {
    display: flex;
    flex-direction: column;
    padding: 0;
}

.accordion-body a {
    font-size: 1.25rem;
    font-weight: 600;
    color: #121212;
    text-decoration: none;
    padding: 0 0 1.5rem 3.5rem;
}

.accordion-body a:hover {
    color: #018fde;
}

/* -----------------------------menu hidden---------------------------- */

div.menu {
    display: none;
}

.menu img {
    width: 2rem;
    margin-left: 2rem;
}

.menu>button {
    border: none;
    background-color: transparent;
}

div .menu_text {
    font-size: 0.875rem;
    line-height: 2rem;
    margin-left: 1rem;
    color: #121212;
}

/* -------------------------------ellipses-------------------------------- */

.ellipse_right {
    z-index: -1;
    position: absolute;
    width: 470px;
    height: 550px;
    left: calc(100vw - 250px);
    top: 100px;
    background: radial-gradient(#c8e1f1 100%, #090e38 10%);
    transform: rotate(-52.45deg);
    filter: blur(150px);
}

.ellipse_left {
    z-index: -1;
    position: absolute;
    width: 360px;
    height: 570px;
    left: 0;
    top: 220px;
    background: radial-gradient(#c8e1f1 100%, #090e38 10%);
    transform: rotate(-52.45deg);
    filter: blur(150px);
}


/* ---------------------------------media--------------------------------- */

@media only screen and (max-width: 768px) {
    div.menu {
        display: flex;
    }

    .navbar_nav {
        display: none;
    }

    .navbar_link:hover {
        color: #018fde;
    }

    nav .active {
    color:#018fde;
    }

    .navbar_link {
        color: #121212;
        font-size: 1.5rem;
        font-weight: 600; 
        line-height: 5rem;
        height: 5rem;
        border-bottom: 1px solid #dbdbdb;
        text-align: start;
        width: 100%;
    }

    #hidden {
        z-index: 1;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        min-width: 100vw;
        min-height: 100vh;
        background-color: #ffffff;
        padding: 11.3rem 4.5rem 4.5rem 4.5rem;
        flex-wrap: nowrap;
    }

    .dropdown_wrapper          {
        display: none;
    }

    .accordion {
        display: flex;
    }
    
    .ellipse_right {
        left: -20%;
        top: -20%;
    }
    
    .ellipse_left {
        left: 60%;
        top: 30vh;
    }
}

@media only screen and (max-width: 576px) {
    .navbar {
        background-color: #ffffff;
    }

    .navbar_buttons>.button_wrapper, .menu_text {
        display: none;
    }

    #hidden {
        padding: 11.3rem 3rem 3rem 3rem;
    }

    button.accordion-button {
        width: calc(100vw - 6rem);
    }

    .ellipse_right {
        width: 240px;
        height: 260px;
        filter: blur(100px);
    }
    
    .ellipse_left {
        width: 190px;
        height: 260px;
        filter: blur(100px);
    }
}

@media only screen and (max-width: 320px) {
    .navbar {
        height: 4rem;
    }

    .navbar_logo {
        width: 4.1rem;
    }

    .navbar_link {
        font-size: 1.25rem;
        line-height: 3.75rem;
        height: 3.75rem;
    }

    #hidden {
        padding: 5.5rem 1.5rem 1.5rem 1.5rem;
    }

    button.accordion-button {
        font-size: 1.25rem;
        width: calc(100vw - 3rem);
        height: 3.75rem;
    }

    .accordion-body a {
        font-size: 1.125rem;
        font-weight: 600;
        color: #121212;
        text-decoration: none;
        padding: 0 0 0.875rem 2.5rem;
    }
}