.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: calc(100vh - 88px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(97, 135, 167);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* ----------------------general----------------------- */

html {
  font-size: 16px;
}

body {
  font-family: 'Montserrat', sans-serif !important;
  font-size: 1rem;
  background: linear-gradient(to top, #EEF3FC, #ffffff) !important;
  background-size: 100vw 100% !important;
  background-repeat: no-repeat !important;
}

html, body {
  overflow-x: hidden;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.conteiner {
  width: 100%;
  max-width: 1440px;
  padding: 0 64px;
  margin: 0 auto;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: fit-content;
  height: fit-content;
}

/* ------------------------media------------------------- */

@media only screen and (max-width: 768px) {
  .conteiner {
    padding: 0 3rem;
  }
}

@media only screen and (max-width: 480px) {
  .conteiner {
    padding: 0 1.5rem;
  }
}