.footer {
  width: 100vw;
  min-height: 22.5rem;
  background-color: #121212;
}

.footer_wrapper {
  max-width: 68rem;
  height: 100%;
  margin: 0 auto;
  padding: 4.5rem 0;
}

.footer_logo {
  width: 5.5rem;
  margin-bottom: 1.2rem;
} 

.footer_dmca {
  width: 5.125rem;
  margin-bottom: 2rem;
}

.footer h3 {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.5rem;
  letter-spacing: 0.01em;
  color: #dbdbdb;
  margin-bottom: 1.2rem;
}

.footer p {
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.5rem;
  color: #dbdbdb;
  padding-bottom: 1rem;
}

.footer_contact>div {
  display: flex;
  justify-content: start;
  align-items: flex-start;
}

.footer_contact>div>p:first-child {
  padding-right: 1.5rem;
}

/* ---------------------------------media--------------------------------- */

@media only screen and (max-width: 767px) {
  .footer_wrapper {
    padding: 2.75rem 0 1.5rem;
  }

  .footer_wrapper>.row {
    margin: 0 0.1rem;
  }

  .footer h3 {
    font-size: 1.125rem;
    line-height: 1.375rem;
  }

  .footer_copyright,
  .footer_contact {
    margin-bottom: 4.5rem;
  }

  .footer_address {
    min-width: 70%;
  }

  .footer_address>.col:first-child {
    width: 40%;
  }

  .footer_address>.col:last-child {
    min-width: 60%;
  }

  .footer_email {
    width: 30%;
  }
}

@media only screen and (max-width: 576px) {
  .footer_address>.col:first-child {
    width: fit-content;
  }

  .footer_address>.col:last-child {
    min-width: fit-content;
  }

  .footer_address {
    min-width: fit-content;
  }

  .footer_email {
    width: fit-content;
  }

  .col {
    padding: 0;
    margin-bottom: 1rem;
  }

  .footer_email {
    margin-bottom: 0;
  }
}

@media only screen and (max-width: 480px) {
  .footer h3 {
    font-size: 1rem;
    line-height: 1.25rem;
  }

  .footer p {
    font-size: 0.75rem;
    line-height: 1.25rem;
    padding: 0;
  }

  .col {
    margin-bottom: 1.5rem;
  }

  .footer_email {
    margin-bottom: 0;
  }
}

